<template>
  <div>
    <tw-header :isAdmin="true" />
    <div class="twp__body">
      <tw-nav class="twp__nav"></tw-nav>
      <div class="twp__main">
        <div class="account">
          <span class="account_icon"></span>
          <span class="account_info">账号完善程度</span>
          <span class="account_nick">您好！{{ this.form.nick }}</span>
        </div>
        <div class="loading">
          <div class="loading_name">进度</div>
          <div class="loadingwrap">
            <div class="loadinginner"></div>
          </div>
          <div class="loading_num">20%</div>
        </div>
        <div class="hr"></div>
        <form @submit.prevent ref="form">
          <div class="form_input">
            <span class="form_input_name">账号:</span
            ><input disabled name="account" required="required" type="text" v-model="form.nick" />
            <div class="form_icon"><img :src="form.nick ? icon : icon1" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">姓名:</span>
            <input name="realName" required="required" type="text" v-model="form.realName" />
            <div class="form_icon"><img :src="form.nick ? icon : icon1" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">性别：</span>
            <input
              class="radio"
              checked
              id="man"
              name="sex"
              type="radio"
              value="1"
              v-model="form.sex"
            /><span class="sexName">男</span>
            <input
              class="radio"
              id="woman"
              name="sex"
              type="radio"
              value="2"
              v-model="form.sex"
            /><span class="sexName">女</span>
            <div class="form_icon"><img :src="icon" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">年龄：</span>
            <input name="age" type="number" v-model="form.age" />
            <div class="form_icon"><img :src="form.nick ? icon : icon1" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">民族：</span>
            <select required v-model="form.nation">
              <option
                :key="index"
                :label="item"
                :value="item"
                v-for="(item, index) in nationsData"
              ></option>
            </select>
            <div class="form_icon"><img :src="icon" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">政治面貌：</span>
            <select required v-model="form.politics">
              <option
                :key="index"
                :label="item"
                :value="item"
                v-for="(item, index) in politicsData"
              ></option>
            </select>
            <div class="form_icon"><img :src="icon" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">身份证号:</span>
            <input name="realName" required="required" type="text" v-model="form.idcard" />
            <div class="form_icon"><img :src="form.nick ? icon : icon1" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">手机号:</span>
            <input disabled name="realName" required="required" type="text" v-model="form.phone" />
            <div class="form_icon"><img :src="form.nick ? icon : icon1" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">微信号:</span>
            <input name="realName" required="required" type="text" v-model="form.wechat" />
            <div class="form_icon"><img :src="form.nick ? icon : icon1" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">邮箱:</span>
            <input name="realName" required="required" type="text" v-model="form.email" />
            <div class="form_icon"><img :src="form.nick ? icon : icon1" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">省：</span>
            <select class="select_region" @change="selectProvince" required v-model="form.province">
              <option
                :key="index"
                :label="item"
                :value="item"
                v-for="(item, index) in regionData"
              ></option>
            </select>
            <span class="form_input_name" style="width:10px;margin:0 2px">市：</span>
            <select class="select_region" @change="selectCity" required v-model="form.city">
              <option
                :key="index"
                :label="item"
                :value="item"
                v-for="(item, index) in provinceData"
              ></option>
            </select>
            <span class="form_input_name" style="width:10px;margin:0 2px">区：</span>
            <select class="select_region" @change="selectCity" required v-model="form.district">
              <option
                :key="index"
                :label="item"
                :value="item"
                v-for="(item, index) in cityData"
              ></option>
            </select>
            <div class="form_icon"><img :src="form.nick ? icon : icon1" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">所在学校（单位）：</span>
            <input name="realName" required="required" type="text" v-model="form.adress" />
            <div class="form_icon"><img :src="form.nick ? icon : icon1" /></div>
          </div>
          <div class="form_input">
            <span class="form_input_name">地址:</span>
            <input name="realName" required="required" type="text" v-model="form.adress" />
            <div class="form_icon"><img :src="form.nick ? icon : icon1" /></div>
          </div>
          <div class="form_input">
            <button class="form_saveButton" @click="onFormConfirm">保存</button>
          </div>
        </form>
      </div>
    </div>
    <tw-footer class="twp__footer" />
  </div>
</template>

<script>
import TwNav from "../../personal/common/nav";
import TwHeader from "../../tuanwei/common/header";
import TwFooter from "../../tuanwei/common/footer";
import { getUserInfo, editUserInfo, getNations, getPolitics, getRegion } from "@/api/user";
export default {
  components: {
    TwNav,
    TwHeader,
    TwFooter
  },
  data() {
    return {
      icon: require("../../../assets/images/sign_icon_right.png"),
      icon1: require("../../../assets/images/home_icon_tips.png"),
      nationsData: [],
      politicsData: [],
      regionData: [],
      provinceData: [],
      cityData: [],
      menuLit: [
        {
          path: "/Personal",
          name: "首页"
        },
        {
          path: "/Info",
          name: "个人信息"
        },
        {
          path: "/signOut",
          name: "查看报名"
        },
        {
          path: "/sumbitProposal",
          name: "提案审核"
        }
      ],
      form: {
        realName: "",
        sex: "",
        age: "",
        phone: "",
        politics: "",
        nation: "",
        idcard: "",
        province: "",
        city: "",
        district: "",
        email: "",
        wechat: "",
        orgaization: "",
        adress: ""
      }
    };
  },
  created() {
    this.getUserInfo();
    this.getSelectData();
  },
  methods: {
    getUserInfo() {
      getUserInfo({}).then(res => {
        this.form = res.data.data;
      });
    },
    getSelectData() {
      getNations().then(res => {
        this.nationsData = res.data.data;
      });
      getPolitics({}).then(res => {
        this.politicsData = res.data.data;
      });
      getRegion({}).then(res => {
        this.regionData = res.data.data;
      });
    },
    selectProvince() {
      getRegion({
        province: this.form.province
      }).then(res => {
        this.provinceData = res.data.data;
      });
    },
    selectCity() {
      getRegion({
        province: this.form.province,
        city: this.form.city
      }).then(res => {
        this.cityData = res.data.data;
      });
    },
    validateForm() {
      let obj = {
        idcard: {
          isIdCardValid: function(id, errMsg) {
            var regCard = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
            if (!id || regCard.test(id)) return;
            return errMsg || "身份证号码是由18位数字组成，请检查格式\n";
          }
        },
        phone: {
          isPhoneValid: function(p, errMsg) {
            var regPhone = /^1[3456789]\d{9}$/;
            if (!p || regPhone.test(p)) return;
            return errMsg || "手机号码是由11为数字组成的，且第1位为1，第2位不能为2，请检查格式\n";
          }
        },
        age: {
          isAgeValid: function(a, errMsg) {
            if (!a) return;
            if (!/^[0-9]+$/.test(a) || a < 0 || a > 60) return errMsg || "年龄需在(0-60)之间\n";
          }
        },
        email: {
          isEmailValid: function(m, errMsg) {
            var regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
            if (!m || regEmail.test(m)) return;
            return errMsg || "邮箱格式是由用户名@域名，请检查格式\n";
          }
        }
      };
      // keys = ['idcard', 'phone']
      let keys = Object.keys(obj);
      let errList = [];
      for (let key of keys) {
        // key = idcard
        let validateObj = obj[key];
        let validateList = Object.keys(validateObj);
        // validateList = ['isIdValid']
        for (let k of validateList) {
          let f = validateObj[k];
          let errMsg = f(this.form[key]);
          if (errMsg) errList.push(errMsg);
        }
      }
      if (errList.length > 0) {
        confirm(errList);
      }
      return errList.length === 0;
    },
    onFormConfirm() {
      this.validateForm();
      editUserInfo(this.form).then(res => {
        if (res.data.code == 200) {
          confirm("修改成功");
          getUserInfo({}).then(res => {
            this.form = res.data.data;
          });
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.twp {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #f4f4f4;
  flex-wrap: wrap;
  min-width: 1200px;
}
.twp__body {
  margin-top: 24px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex: 1;
}
.twp__main {
  width: 882px;
  margin-left: 24px;
  padding: 36px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
}
.twp__nav {
  flex: 0 0 294px;
}
.personal {
  display: flex;
  height: 717px;
  .personal_menu {
    flex: 1;
  }
  .personal_cont {
    flex: 2;
    position: relative;
  }
}
ul {
  list-style: none;
}
.form_input {
  display: flex;
  position: relative;
  flex: 1;
  font-size: 14px;
  margin-bottom: 10px;
  .form_input_name {
    width: 150px;
  }
}
.form_icon {
  position: absolute;
  right: 20px;
  width: 30px;
  height: 30px;
}
.form_saveButton {
  width: 320px;
  height: 40px;
  background: #ff543c;
  border-radius: 6px;
  margin: 0 auto;
  border: none;
  outline: none;
  color: #ffffff;
}
input,
select {
  width: 280px;
  height: 30px;
  outline: none;
  border: 1px solid #cccccc;
  border-radius: 4px;
  margin: -10px 0 10px 20px;
}
option {
  outline: none;
  background: #ffffff;
  box-shadow: 0 2px 18px 0 rgba(225, 225, 225, 0.8);
  border-radius: 4px;
}
.radio {
  width: 20px;
}
.sexName {
  margin-top: -5px;
  margin-left: 10px;
}
.select_region {
  width: 180px;
  height: 30px;
}
.account {
  margin: 0;
  .account_icon {
    width: 3px;
    height: 20px;
    background: #ff543c;
    border-radius: 2.5px;
  }
  .account_nick {
    float: right;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #2d2d2d;
  }
}
.account_info {
  font-family: PingFangSC-Medium;
  font-size: 20px;
  color: #2d2d2d;
  line-height: 20px;
  &::after {
    content: "(建议您完善个人信息，以便进行报名，提交提案后续操作)";
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #828282;
  }
}
.loading {
  display: flex;
  .loadingwrap {
    width: 74%;
    height: 15px;
    position: relative;
    margin: 1.5rem auto 0;
    background: #f0f0f0;
    border-radius: 2px;
    padding-right: 30px;
  }
  .loadinginner {
    position: absolute;
    height: 100%;
    width: 20%;
    background: #3fce60;
    border-radius: 2px 0 0 2px;
  }
}
.loading_name {
  margin-top: 20px;
}
.loading_num {
  margin-top: 20px;
}
.hr {
  background: #eaeaea;
  width: 77%;
  height: 1px;
  margin: 30px auto;
}
</style>
